html {
    box-sizing: content-box;
}

html * {
    box-sizing: inherit;
}

header {
    display: flex;
    justify-content: center;
    background-color: #fff;
    padding: 22px;
    box-shadow: 1px 2px 5px rgba(0,0,0,0.3);
    width: 100%;
    z-index: 2;
    position: relative;
}

header input {
    outline: none;
    border: 1px solid rgb(150, 150, 150);
    padding: 10px 15px;
}